.mobile-log-home {
  /* padding-top: 80px; */
  height: 100%;
  background: #f4f9f5 0% 0% no-repeat padding-box;
  overflow-y: scroll;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  width: 250px;
  box-shadow: 0px 3px 32px #6464644A;
}

.mobile-log-home::-webkit-scrollbar {
  width: 4px;
}

.mobile-log-home::-webkit-scrollbar-track {
  background: lightgray;
}

/* scrollbar handle */
.mobile-log-home::-webkit-scrollbar-thumb {
  background-color: #989b9e;
}
