
.mobile-instruction-main-div {
  padding: 20px 10px;
  background: #f4f9f5;
  height: 100%;
  width: 250px;
  box-shadow: 0px 3px 32px #6464644A;
}

.mobile-instructions-heading .MuiTypography-body1 {
  font-size: 16px;
  color: #04724d;
  font-weight: bold;
  /* font-family: Ubuntu; */
  line-height: 1.mobile-2;
}
.mobile-instructions-subHeading .MuiTypography-body1 {
  font-size: 9px;
  color: #000000;
  opacity: 60%;
}

.mobile-instructions-logo{
  width: 80px;
}
.mobile-instructions-logo img{
  width: 100%;
}

.mobile-main-wrapper{
  display: flex;
  justify-content: space-between;
}

/* .mobile-page-content {
  padding: 0 150px;
  margin-top: 30px;
} */

.mobile-mainstepper-mobile{
  display: none;
}
.mobile-button-new{
  display: none;
}
.mobile-page-content .MuiPaper-root {
  background: transparent;
}

.mobile-page-content .MuiStepLabel-label.MuiStepLabel-alternativeLabel {
  display: none;
}

.mobile-page-content .MuiStepConnector-alternativeLabel {
  display: none;
}

.mobile-mainstepper {
  /* width: 100%; */
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.mobile-page-content .MuiSvgIcon-root {
  width: 30px;
  height: 30px;
  margin: 0 5px;
}

 .mobile-ButtonInstructions {
  width: 100px !important;
  font-size: 11px !important;
  height: 30px !important;
 }
 .mobile-Button1 {
  font-size: 13px !important;
  background-color: #04724d !important;
  color: #ffffff !important;
  width: 150px;
  border-radius: 15px;

}

 .MuiButton-contained{
  border-radius: 4px;
}

.mobile-page-content .MuiButton-contained:hover {
  background-color: #04724d;
}

.mobile-page-content .MuiStepIcon-root.MuiStepIcon-active {
  color: #04724d;
}

.mobile-page-content .MuiStepIcon-root.MuiStepIcon-completed {
  cursor: pointer;
  color: #04724d;
}
.mobile-page-content .MuiStepIcon-root {
  color: #04724d1a;
}

/* .mobile-Button {
  background-color: #04724D;
  color: #FFFFFF;
  
} */

.mobile-contents-verbal {
    padding: 10px;
    border-radius: 4px;
    background-color: #ffffff;
    width: 80%;
    margin: 60px auto 0 auto;
 
}

.mobile-li-2{
  font-size: 11px;
  margin-bottom: 10px;
  color: #000000 !important;
  opacity: 1;
  font-weight: 500;
  display: flex;
  align-items: center;
}

.mobile-dot{
  height: 5px;
  width: 5px;
  background: #000;
  border-radius: 50%;
  margin-right: 10px;
}

.mobile-arrowChanges {
  width: 40px !important;
  height: 27px !important;
  float: right;
}
.mobile-buttonNextFlex {
  display: flex;
  justify-content: space-between;
}

.mobile-continue-button-wrapper{
    margin-top: 100px;
    display: flex;
    justify-content: flex-end;
}

.mobile-continue-button{
    width: 100px !important;
    font-size: 11px !important;
    height: 30px!important;
}



