.main-mobile{
    background:#EDEFF2;
    box-shadow: 0px 3px 32px #6464644A;
}

.mobile2-instructions-heading .MuiTypography-body1 {
  font-size: 16px;
  color: #04724d;
  font-weight: bold;
  line-height: 1.mobile2-2;
}

.mobile2-instructions-logo img{
  width: 100%;
}

.mobile2-main-wrapper{
  display: flex;
  justify-content: space-between;
}

.mobile2-page-content .MuiPaper-root {
  background: transparent;
}

.mobile2-page-content .MuiStepLabel-label.MuiStepLabel-alternativeLabel {
  display: none;
}

.mobile2-page-content .MuiStepConnector-alternativeLabel {
  display: none;
}


.mobile2-page-content .MuiSvgIcon-root {
  width: 30px;
  height: 30px;
  margin: 0 5px;
}

 .mobile2-Button {
  background-color: #04724d !important; 
  color: #ffffff !important;
  width: 100px !important;
  border-radius: 15px;
  display: flex;
  justify-content: space-between;
  font-size: 13px !important;
  height: 30px !important;
 }
 .mobile2-Button1 {
  font-size: 13px !important;
  background-color: #04724d !important;
  color: #ffffff !important;
  width: 150px;
  border-radius: 15px;
  height: 30px !important;

}

.mobile2-ButtonNew{
  height: 30px!important;
  font-size: 11px !important;
  width: 100px !important;
}

 .MuiButton-contained{
  border-radius: 4px;
}

.mobile2-page-content .MuiButton-contained:hover {
  background-color: #04724d;
}

.mobile2-page-content .MuiStepIcon-root.MuiStepIcon-active {
  color: #04724d;
}

.mobile2-page-content .MuiStepIcon-root.MuiStepIcon-completed {
  cursor: pointer;
  color: #04724d;
}
.mobile2-page-content .MuiStepIcon-root {
  color: #04724d1a;
}

