}

.li-mobile {
    font-size: 9px;
    margin-bottom: 5px;
  color: #000000 !important;
  opacity: 1;
  font-weight: 500;
}

.mobile2-arrowChanges {
  width: 40px !important;
  height: 27px !important;
  float: right;
}
.mobile2-buttonNextFlex {
  display: flex;
  justify-content: space-between;
}


