.main-preview-wrapper{
    // width: 1086px;
    background: #fff;
    margin: 20px auto;
    border-radius: 9px;

    


    .step-label > span > svg {
        width: "50px";
        height: "50px";
      }
      
      
      .previewMainstepper .MuiPaper-root {
        background-color: transparent !important;
        width: 100%;
        margin: 0 auto;
      }
      .previewMainstepper .MuiStepConnector-root {
        color: #E9F3EF;
        background: #E9F3EF;
        margin-bottom: 25px;
        margin-left: 10px;
        margin-right: 10px;
      
      }
      
      .previewMainstepper .MuiStepLabel-label.MuiStepLabel-active{
        font-size: 12px;
        font-weight: 600;
        color: #333;
      }
      
      .previewMainstepper .MuiStepLabel-label{
        font-size: 12px;
        font-weight: 600;
        color: #333;
      
      }
      .previewMainstepper .MuiStepLabel-root{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
      .MuiStepIcon-root.MuiStepIcon-active {
        color: #04724d !important;
        cursor: pointer;
      }
      .MuiStepIcon-root.MuiStepIcon-completed {
        color: #59c99c !important;
      }
      .previewMainstepper .MuiStepConnector-alternativeLabel {
        top: 20px !important;
        left: calc(-47% + 18px) !important;
        right: calc(53% + 18px) !important;
        position: absolute !important;
      }
      
      .previewMainstepper .MuiStepConnector-lineHorizontal {
        border-top-width: 1px !important;
      }
      
       .MuiStep-horizontal{
        padding: 0 !important;
      }
      
      .mainDiv .MuiStepConnector-lineHorizontal {
        border-top-style: dashed !important;
      }
      
      .previewMainstepper .MuiStepConnector-line {
        /* border-color: #ffffff !important; */
      }
      
      .previewMainstepper .MuiPaper-root .MuiSvgIcon-root {
        // fill: #EDEFF2;
        width: 50px;
        height: 50px;
        cursor: pointer;
      }
      
      .previewMainstepper .MuiStepIcon-root{
        color: #EDEFF2;
      }
      
      .MuiStepIcon-root.MuiStepIcon-completed{
       color: #21745D !important;
      }
      
      
      .previewMainstepper .MuiStepLabel-iconContainer{
        padding-left: 0 !important;
      }
      
      .previewMainstepper.MuiStepIcon-text {
        cursor: pointer;
      
       
      }


      .preview-body{
          height: 410px;
          width: 846px;
          margin: 20px auto;
          // border: 1px solid #dadada;
          display: flex;
          justify-content: center;
    
      }

 

  
}

