
body,
html {
  margin: 0;
  padding: 0;
}

#root{
  overflow-y: hidden;
}

.mobile2-preview-main-div {
  padding: 0px  15px;

}



.mobile2-previews-subHeading1 .MuiTypography-body1 {
  /* font: normal normal normal 25px/38px Poppins; */
  font-size:18px;
  opacity: 0.mobile2-6;
}
.mobile2-previews-subHeading .MuiTypography-body1 {
  font-size: 30px;
  color: #000000;
  opacity: 1;
  font-family: Poppins;
  font-weight: bold;
}


.mobile2-flex-div-new{
    display: flex;
    justify-content: space-between;
    align-items: center;
}



.mobile2-preview-video {
  position: relative;
  background-color: black;
  height: 500px;
  border-radius: 30px;
}
.mobile2-preview-video video {
  height: 500px;
  /* width: 700px; */
  object-fit: fill;
  border-radius: 25px;
}


.mobile2-Button1 {
  margin-top: 50px;
  background-color: #04724d;
  color: #ffffff;
}


.mobile2-controls {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 0px;
  margin-bottom: 24px;
  left: 236px;
}
.mobile2-videoHandle {
  padding: 0px;
  margin-right: 20px;
}
.mobile2-candidateName {
  font: normal normal 600 25px/38px Poppins;
  position: absolute;
  top: 0px;
  left: 25px;
  margin-top: 10px;
}
.mobile2-candidateName span {
  margin-left: 10px;
}


.mobile2-control2 {
  width: 45px;
  height: 45px;
  background-color: #04724d;
  border-radius: 50%;
  padding-top: 10px;
}
.mobile2-control1 {
  width: 45px;
  height: 45px;
  background-color: #f03636;
  border-radius: 50%;
  padding-top: 10px;
}

  .mobile2-second-wrapper{
    padding: 0 10px 10px 10px;
    background: #fff;
    width: 100%;
    }
    .mobile2-access-text{
      font-size: 7px !important;
      margin-left: 5px;
    }
  
    .mobile2-flex-div-preview{
      border: 1px solid #04724D57;
      padding: 5px ;
      margin: 5px;
      border-radius: 4px;
      display: flex;
      width: 150px;
 
    }
    .mobile2-interview-video{
      background: #fff;
      display: flex;
      justify-content: center;
    }
   .mobile2-page-content-preview{
     margin: 0;
   }
   .mobile2-previews-heading{
     margin: 0;
   }
   .mobile2-preview-main-div{
     padding: 0;
     width: 250px;
     box-shadow: 0px 3px 32px #6464644A;
   }
  .mobile2-interview-video video {
    width: 768px;
    border-radius: 0px;
    object-fit: cover;
    object-position: center;
  }
  .mobile2-main-flex-new{
    display: none !important;
  }
  .mobile2-only-tab-div{
    display: flex !important;
  }

 
  .mobile2-previews-heading .MuiTypography-body1{
    font-size: 24px;
    color: #04724d;
    font-weight: bold;
    font-size: 30px;
    line-height: 1;
    margin-bottom: 8px;
  }
  .mobile2-previews-subHeading1 .MuiTypography-body1{
    font-size: 12px;
  }
  .mobile2-birbal-logo-preview{
    display: none !important;
  }
  .mobile2-birbal-logo-preview2{
    display: block;
  }

  .mobile2-turn-on-2{
    display: block;
    font-size: 11px;
    font-weight: 600;
  }
  .mobile2-row-wrapper{
    flex-direction: column;
  }
  .mobile2-left-div-new{
    width: 100%;
  }

  .mobile2-right-div-new{
   display: none;
  }
  .mobile2-preview-start{
    margin: 0;
  }

  .mobile2-audio-bar{
    display: block !important;
    width: 100%;
    padding: 10px 0;
  }

  .mobile2-only-mobile2-button{
    display: flex !important;
    justify-content: center;
    align-items: center;
  }
  .mobile2-FirstModal{
    width: 90% !important;
    margin: 0 auto;
  }
  .mobile2-dialog-wrapper{
    flex-direction: column;
    display: flex;
    padding: 30px;
    border-radius: 15px;
    justify-content: space-between;
    align-items: center;
  }
  .mobile2-unstable-text-1{
    display: none;
  }
  .mobile2-unstable-text-2{
    padding: 10px 0;
    font-size: 14px;
    font-family: 'Poppins', sans-serif;
    opacity: 0.mobile2-8;
  }
  .mobile2-Modal-div{
    font-size: 12px;
    border-radius: 4px !important;
    padding: 10px !important;
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .mobile2-right-div{
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
  .mobile2-flex-div-preview-2{
    justify-content: space-between ;
    display: flex;
    align-items: center;
  }
   .mobile2-div-1{
    width: 5% !important;
    display: flex;
    justify-content: flex-end;
  }

   .mobile2-div-2{
    width: 90% !important;
    transform: rotate(
        180deg
        );
  }

  .mobile2-audio-bar .MuiLinearProgress-barColorPrimary{
      background: #04724d !important;
  }
 




.mobile2-Message {
  font-size: 30px;
  color: red;
