}

.button-div {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0;
}

.Button-preview{
  width: 150px !important;
  height: 28px !important;
  font-size: 11px !important;
}

.upload-file .MuiButton-contained {
  font-size: 14px;
  border-radius: 20px;
  padding: 13px 48px;
  background-color: #a9c9b2;
  width: 242px;
  margin-top: 5px;
  /* margin-top: -27px; */
}

.upload-file .MuiButton-contained:hover {
  box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%),
    0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
  background-color: #a9c9b2;
}

.button-div .MuiButton-contained:hover {
  background-color: #04724d;
}

@media screen and (max-width: 425px) {
  .upload-file .MuiButton-contained {
    padding: 13px 0px;
    width: 186px;
  }
}

