.instruction-main-div {
  padding: 20px 30px;
  background: #f4f9f5;
  height: 100%;
}

.instructions-heading .MuiTypography-body1 {
  font-size: 24px;
  color: #04724d;
  font-weight: bold;
  /* font-family: Ubuntu; */
  line-height: 1.2;
}
.instructions-subHeading .MuiTypography-body1 {
  font-size: 13px;
  color: #000000;
  opacity: 60%;
}

.instructions-logo{
  width: 100px;
}
.instructions-logo img{
  width: 100%;
}

.main-wrapper{
  display: flex;
  justify-content: space-between;
}

/* .page-content {
  padding: 0 150px;
  margin-top: 30px;
} */

.mainstepper-mobile{
  display: none;
}
.button-new{
  display: none;
}
.page-content .MuiPaper-root {
  background: transparent;
}

.page-content .MuiStepLabel-label.MuiStepLabel-alternativeLabel {
  display: none;
}

.page-content .MuiStepConnector-alternativeLabel {
  display: none;
}

.mainstepper {
  /* width: 100%; */
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.page-content .MuiSvgIcon-root {
  width: 30px;
  height: 30px;
  margin: 0 5px;
}

 .ButtonInstructions {
  width: 100px !important;
  font-size: 11px !important;
  height: 30px !important;
 }
 .Button1 {
  font-size: 13px !important;
  background-color: #04724d !important;
  color: #ffffff !important;
  width: 150px;
  border-radius: 15px;

}

 .MuiButton-contained{
  border-radius: 4px;
}

.page-content .MuiButton-contained:hover {
  background-color: #04724d;
}

.page-content .MuiStepIcon-root.MuiStepIcon-active {
  color: #04724d;
}

.page-content .MuiStepIcon-root.MuiStepIcon-completed {
  cursor: pointer;
  color: #04724d;
}
.page-content .MuiStepIcon-root {
  color: #04724d1a;
}

/* .Button {
  background-color: #04724D;
  color: #FFFFFF;
  
} */

.contents-verbal {
    padding: 25px;
    border-radius: 4px;
    background-color: #ffffff;
    width: 350px;
    margin: 60px auto 0 auto;
 
}

.li-2{
  font-size: 13px;
  margin-bottom: 10px;
  color: #000000 !important;
  opacity: 1;
  font-weight: 500;
  display: flex;
  align-items: center;
}

.dot{
  height: 5px;
  width: 5px;
  background: #000;
  border-radius: 50%;
  margin-right: 10px;
}

.arrowChanges {
  width: 40px !important;
  height: 27px !important;
  float: right;
}
.buttonNextFlex {
  display: flex;
  justify-content: space-between;
}





@media (max-width: 1024px) {
  .instruction-main-div{
    padding: 30px 20px 0 20px;
  }
  .Button,.Button1{
    font-size: 13px !important;
  }

  .instructions-heading .MuiTypography-body1 {
    font-size: 18px;
  }
  .instructions-subHeading .MuiTypography-body1 {
    font-size: 11px;
    opacity: 100%;
    padding: 10px 0;
  }


  /* .page-content {
    padding: 0 45px;
  } */


  .mainstepper{
    display: none;
  }
  .mainstepper-mobile{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0  auto;
  }
  .button-new{
    display: flex;
    position: fixed;
    bottom: 0;
    padding: 10px;
    width: 100%;
    background: #fff;
    justify-content: flex-end;
  }
  .page-content .MuiButton-contained {
    width: 120px;
    font-size: 10px;
    padding: 6px;
  }

  .page-content .MuiStepper-root {
    padding: 15px 0 15px 0;
  }



}

.continue-button-wrapper{
    margin-top: 40px;
    display: flex;
    justify-content: flex-end;
}

.continue-button{
    width: 120px !important;
}



